import { lazy, Suspense } from "react";
import Header from "./components/header/header";
import "./App.css";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
// import MainPage from "./components/pages/MainPage";
// import { MainPage, ComicsPage, SingleComics } from "./components/pages";
import Spinner from "./components/spinner/Spinner";
const Error404 = lazy(() => import("./components/pages/404"));
const MainPage = lazy(() => import("./components/pages/MainPage"));
const ComicsPage = lazy(() => import("./components/pages/ComicsPage"));
const SingleComics = lazy(() => import("./components/pages/SingleComicsPage"));
const App = () => {
  return (
    <Router>
      <div className="App">
        <Header />
        <Suspense fallback={<Spinner />}>
          <Routes>
            <Route end path="/comics/:id" element={<SingleComics />} />
            <Route exact="true" path="/comics" element={<ComicsPage />} />
            <Route exact="true" path="/marvel" element={<MainPage />} />

            <Route path="*" element={<Error404 />} />
          </Routes>
        </Suspense>
      </div>
    </Router>
  );
};

export default App;
